section {
	display: flex;
	justify-content: space-around;
	align-content: space-around;
	flex-wrap: wrap;
	background-color: #5d99a9;
}

.link {
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	align-items: center;
	width: 40vw;
	height: 35vh;
	margin: 5vw;
	background-color: hsl(30, 48%, 67%);
	border-radius: 10px;
}

.link h3 {
	color: hsl(174, 72%, 30%);
}

.link img {
	border-radius: 50%;
	width: 30vw;
	height: 30vw;
}

article {
	width: 80vw;
	padding: 5vh 0 5vh 0;
	margin: 5vh 0 5vh 0;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	align-items: center;
	border-radius: 20px;
}

article#personal {
	background-color: hsl(198, 50%, 32%);
}

article#questions {
	background-color: #5d99a9;
}

section#personal {
	width: 100vw;
	background-color: hsl(30, 48%, 67%);
	display: flex;
	flex-direction: column;
}

article img {
	width: 50vw;
	height: 50vw;
	border-radius: 50%;
}

article h3 {
	color: hsl(000, 0%, 80%);
}

#return {
	padding: 0 10px 0 10px;
	border-style: solid;
	border-width: 2px;
	border-color: hsl(24, 100%, 50%);
	border-radius: 10px;
	transition: background-color 0.5s;
}

#return:hover {
	transition: background-color 0.5s;
	background-color: hsla(24, 100%, 70%, 0.3);
}

td {
	font-size: 4vw;
	text-align: center;
	border-color: #555555;
	border-style: solid;
	border-width: 0 0 1px 0;
}

table {
	font-family: "Amatic SC", cursive;
	margin: 0 5vw 0 5vh;
}

tr td:first-child {
	font-weight: bold;
}

@media (min-width: 768px) {
	.link {
		width: 30vw;
		height: 50vh;
	}
	.link img {
		width: 15vw;
		height: 15vw;
	}
	article {
		width: 40vw;
	}
	h3, h4 {
		font-size: 2vw;
	}
	td {
		font-size: 1.5vw;
	}
	article img {
		width: 20vw;
		height: 20vw;
	}
	section#personal {
		flex-direction: row;
	}
}
